  </div>
</template>

<script>
  import {folderListAndChildTree, deleteCategory, goUpOrDown} from '@/api/fileManage'

  const floorNum = 0
  export default {
    data() {
      return {
        index: 1,
        listQuery: {
          page: 1,
          pageSize: 10,
          folderId: '',
          companyId: this.$store.getters.companyId
        },
        tableData: [],
        // 
        routerList: [
          {'key': '/', 'val': null}
        ],
        // 
        total: '',
        isShowAllText: false,
        folderName: '',

        //
        tempData: [],

        allowAdd: this.authorityControl('SYSTEM:FILE:MANAGE', 'SYSTEM:FILE:FILECLASS', 'SYSTEM:FILE:FILECLASS:INSERT'),//
        allowUpdate: this.authorityControl('SYSTEM:FILE:MANAGE', 'SYSTEM:FILE:FILECLASS', 'SYSTEM:FILE:FILECLASS:UPDATE'),//
        allowDetail: this.authorityControl('SYSTEM:FILE:MANAGE', 'SYSTEM:FILE:FILECLASS', 'SYSTEM:FILE:FILECLASS:DETAILS'),//
        allowDelete: this.authorityControl('SYSTEM:FILE:MANAGE', 'SYSTEM:FILE:FILECLASS', 'SYSTEM:FILE:FILECLASS:DELETE'),//
        allowUpDown: this.authorityControl('SYSTEM:FILE:MANAGE', 'SYSTEM:FILE:FILECLASS', 'SYSTEM:FILE:FILECLASS:UPORDOWN'),//
      }
    },
    filters: {},
    activated() {

      // this.listQuery.page = 1;
      // this.listQuery.pageSize = 10;
      if(this.$store.getters.fileTypePage != 0){
        this.listQuery.page = 1;
        this.listQuery.pageSize = this.$store.getters.fileTypePage;
        this.getList();
      }else{
        this.listQuery.page = 1;
        this.listQuery.pageSize = 4;
        this.getList();
      }
    },
    beforeRouteLeave(to, from, next){
      let position = document.documentElement.scrollTop
      this.$store.commit('SET_FILETYPE_SCROLL', position) //
      this.$store.commit('SET_FILETYPE_PAGE', this.tempData.length) //
      next()
    },
    methods: {
      handleSearch() {
        this.listQuery.page = 1;
        this.listQuery.pageSize = 10;
        this.listQuery.folderName = this.folderName;
        this.getList()
      },


      getList() {
        folderListAndChildTree(this.listQuery).then(res => {
          if (res.code == 200) {
            if (this.listQuery.page > 1) {
              this.tempData = this.tempData.concat(res.data.data);
            } else {
              this.tempData = res.data.data;

              // if(this.$store.getters.fileTypePage != 0){
              //   // this.listQuery.page = this.$store.getters.fileTypePage;
              //   // this.listQuery.pageSize = 4;
              // }
            }
            this.recursionList(this.tempData, floorNum);
            this.total = res.data.total;
          } else {
            this.$message.error(res.message ? res.message : ',!')
          }
        })
      },

      //
      getMore() {
        this.listQuery.page += 1;
        this.getList();
      },

      // 
      recursionList(data, floorNum) {
        const that = this
        that.tableData = data.map((item) => {
          item.floor = floorNum + 1
          item.show = true
          if (item.child && item.child.length !== 0) {
            that.recursionList(item.child, item.floor)
          }
          return item
        })
      },

      // 
      handleDelete(id) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let params = {
            folderId: id,
            companyId: this.$store.getters.companyId
          }
          deleteCategory(params).then(res => {
            if (res.code == 200) {
              this.$message({type: 'success', message: ''})
              this.getList()
            } else {
              this.$message({type: 'error', message: res.message})
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },

      //  
      handleSort(row, status) {
        const params = {
          operationType: status,
          parentId: row.parentId,
          folderId: row.folderId,
          levelNum: row.levelNum
        }
        goUpOrDown(params).then(res => {
          if (res.code == 200) {
            this.$message({type: 'success', message: res.message})
            this.getList()
          } else {
            this.$message({type: 'error', message: res.message})
          }
        })
      },

      // 
      isShowHandle(data) {
        this.tableData = this.tableData.map(item=>{
          if(item.folderId == data.folderId){
            item.show = !item.show;
          }
          return item;
        })
      },

      // 
      handleName(item) {
        let flag = true;
        // this.listQuery.folderName = item.folderName
        const obj = {
          key: `${item.folderName}/`,
          val: item.folderId
        }

        if (item.child != '') {
          this.routerList.forEach(data => {
            if (data.val != obj.val) {
              flag = true;
            } else {
              flag = false;
            }
          })

          if (flag) {
            this.routerList.push(obj)
          }

          this.listQuery.folderId = item.folderId;
          this.listQuery.levelNum = item.levelNum;

          this.listQuery.page = 1;
          this.listQuery.pageSize = 10;

          this.getList()
        }
      },

      // 
      handleRouteItem(name) {
        this.listQuery.folderId = name
        this.routerList.forEach((item, index) => {
          if (name === item.val) {
            this.routerList = this.routerList.slice(0, index + 1)
          }
        })
        this.getList()
      },

      // 
      showALLHandle() {
        this.isShowAllText = !this.isShowAllText
        this.tableData.forEach(item => {
          item.show = !item.show
        })
      },

      //
      goAddFile() {
        this.$router.push({
          path: '/file/addFileType'
        })
      },

      //
      goAddSonCategory(item) {
        this.$router.push({
          path: '/file/addFileType',
          query: {
            parentId: item.folderId,
            parentIds: item.parentIds,
            levelNum: item.levelNum + 1,
            parentName: item.parentName == null ? item.folderName : item.parentName
          }
        })
      },

      //
      handleEdit(item) {
        this.$router.push({
          path: '/file/editFileType',
          query: {
            folderId: item.folderId,
            parentId: item.parentId,
            parentIds: item.parentIds,
            levelNum: item.levelNum,
            folderName: item.folderName
          }
        })
      }

    }
  }
</script>
