                white-space: nowrap;
                cursor: pointer;
                /*font-weight: bold;*/

                &:hover {
                  color: #05AAFF;
                }
              }
            }

            .table-handle-bar {
              display: flex;
              align-items: center;
              width: 100%;
              flex-wrap: wrap;

              .item-bar {
                font-size: 14px;
                margin-right: 10px;
                cursor: pointer;
                display: flex;
                align-items: center;
                /*margin-bottom: 10px;*/

                &.add, &.edit {
                  color: #05AAFF;
                }

                &.del {
                  color: #F56C6C;
                }

                &.sort {
                  color: #FFA96A;
                }

                img {
                  vertical-align: baseline;
                  margin-right: 5px;
                }
              }
            }
          }
        }

        .children {
          margin-bottom: 10px;
          background: #E9F8FF;

          .floor {
            .person {
              .item:first-child {
                padding-left: 37px;
              }

              .item {
                .num {
                  width: 24px;
                  height: 24px;
                  background: rgba(145, 229, 161, 1);
                  border-radius: 50%;
                  margin-right: 5px;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  color: #fff;
                }
              }
            }

            &:last-child {
              margin-bottom: 0;
            }
          }

          .floor:nth-child(even) {
            background: #DEE6FF;

            &:hover {
              background: #C5D4FF;
            }
          }

          .floor:nth-child(odd) {
            background: #EFFAFF;

            &:hover {
              background: #CDEEFF;
            }
          }
        }
      }
    }
  }

  .route-list {
    margin-top: 10px;
    background: #fff;
    padding: 10px 20px;
    display: flex;
    font-size: 14px;
    cursor: pointer;

    .item:hover {
      color: #05AAFF;
    }
  }

  .roleChoose {
    border: 1px solid #C0C4CC;
    padding: 10px;
    border-radius: 4px;
  }

  .noMore {
    text-align: center;
    padding: 20px 0;
  }

  .get-more {
    padding: 30px 0;
    text-align: center;
    cursor: pointer;
  }

</style>



